:root {
  --text-primary: #111827;
  --text-secondary: #4b5563;
  --bg-main: #f9fafb;
  --bg-hover: #f3f4f6;
  --border-color: #e5e7eb;
  --accent-gradient: linear-gradient(to right, #4f46e5, #7c3aed);
  --accent-hover: linear-gradient(to right, #6366f1, #8b5cf6);
  --toggle-on-bg: #4f46e5;
  --toggle-off-bg: #d1d5db;
  --transition-speed: .2s;
}

body {
  background-color: var(--bg-main);
  width: 280px;
  color: var(--text-primary);
  margin: 0;
  font-family: system-ui, -apple-system, sans-serif;
}

.popup-header {
  border-bottom: 1px solid var(--border-color);
  background: #fff;
  align-items: center;
  gap: .6rem;
  padding: 1rem;
  display: flex;
}

.logo-icon {
  color: #4338ca;
  width: 1.75rem;
  height: 1.75rem;
}

.popup-header h1 {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
}

.popup-content {
  padding: 1rem;
}

.dashboard-btn {
  color: #fff;
  background: var(--accent-gradient);
  cursor: pointer;
  width: 100%;
  transition: all var(--transition-speed) ease-out;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  gap: .6rem;
  padding: .75rem 0;
  font-size: .9rem;
  font-weight: 500;
  display: flex;
  box-shadow: 0 1px 3px #0000001a, 0 1px 2px #0000000f;
}

.dashboard-btn:hover {
  background: var(--accent-hover);
  transform: translateY(-2px);
  box-shadow: 0 7px 15px #0000001a, 0 3px 6px #0000000d;
}

.dashboard-btn svg {
  width: 1.25rem;
  height: 1.25rem;
}

.divider {
  border: none;
  border-top: 1px solid var(--border-color);
  margin: 0;
}

.popup-footer {
  flex-direction: column;
  gap: .75rem;
  padding: .75rem 1rem;
  display: flex;
}

.toggle-row {
  transition: background-color var(--transition-speed) ease;
  border-radius: 6px;
  justify-content: space-between;
  align-items: center;
  padding: .5rem;
  display: flex;
}

.toggle-row label {
  color: var(--text-secondary);
  font-size: .9rem;
  font-weight: 500;
}

.switch {
  width: 44px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: var(--toggle-off-bg);
  transition: background-color var(--transition-speed);
  border-radius: 34px;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  width: 18px;
  height: 18px;
  transition: transform var(--transition-speed) cubic-bezier(.25, 1, .5, 1);
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  bottom: 3px;
  left: 3px;
  box-shadow: 0 1px 2px #0000001a;
}

input:checked + .slider {
  background: var(--toggle-on-bg);
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.toggle-row:hover {
  background-color: var(--bg-hover);
}
